.opentextinput__answer-container {
    color: black;
}
.trialogue__bottom-bar {
    position: relative !important;
}

.opentextinput__count-characters-container {
    margin-bottom: 10px;
}
.trialogue__widget {
    display: flex;
    min-height: 24rem;
    padding-inline: 40px;padding-block: 20px;position: relative;
    flex-direction: column;
    justify-content: space-between;
    /* background-color:#ccc; */
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.trialogue__messages-history,.trialogue__message {
    flex: 100%;
}

.trialogue__message-guest {
    text-align: right;
    background-color: #e6e6e6;
    border-radius: 10px;
    /* box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.trialogue__message-meta {
    text-align: center;
    color: #666
}

.trialogue__message-host {
    text-align: left;
    color: white;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    background-color: #2f529f;
    border-radius: 10px
}

.trialogue__message__inner,.trialogue__messages-history__inner {
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 5px;
    /* box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px; */
}

.trialogue__bottom-bar {
    position: fixed;
    bottom: 1rem;
    width: 100%;
    background-color: #ccc
}

@media (min-width: 900px) {
    .trialogue__bottom-bar {
        max-width:885px;
        margin: 0 auto
    }
}

.trialogue__bottom-bar__buttons {
    padding: 1rem;
    display: flex;
    justify-content: center;
    gap: 4rem;
    background-color: white;
    align-items: center
}

.trialogue__bottom-bar__buttons .btn-text {
    background-color: #e6e6e6;
    color: #666;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
    border: solid 3px #4d4d4d;
    border-radius: 5px;
    font-weight: 500 !important;
}

.no-touch .trialogue__bottom-bar__buttons .btn-text:not(.is-disabled):not(.is-locked):hover {
    background-color: #e6e6e6;
    color: #222
}

.trialogue__bottom-bar__buttons .btn-text.is-disabled {
    visibility: hidden
}